  --font-weights-medium: 500;
  --font-weights-semibold: 600;
  --font-weights-bold: 700;
  --letter-spacings-inherit: inherit;
  --letter-spacings-none: 0;
  --letter-spacings-normal: 0.01em;
  --letter-spacings-wide: 0.03em;
  --line-heights-4: 1rem;
  --line-heights-5: 1.25rem;
  --line-heights-6: 1.5rem;
  --line-heights-7: 1.75rem;
  --line-heights-9: 2.25rem;
  --line-heights-inherit: inherit;
  --opacity-0: 0;
  --opacity-50: 0.5;
  --opacity-80: 0.8;
  --opacity-100: 1;
  --radii-none: 0;
  --radii-xs: 0.25rem;
  --radii-sm: 0.5rem;
  --radii-md: 0.75rem;
  --radii-lg: 1rem;
  --radii-circle: 9999px;
  --space-0: 0;
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
