}* {
  background: unset;
  border: unset;
  color: unset;
  font-family: var(--fonts-body);
  font-stretch: unset;
  font-style: unset;
  font-variant: unset;
  font-weight: unset;
  list-style-type: none;
  margin: 0;
  outline: unset;
  padding: 0;
  text-decoration: unset;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
*:focus-visible {
  outline: none;
  box-shadow: 0 0 0 var(--border-widths-thick) var(--colors-border-focus) inset;
  border-color: transparent;
}
body {
  background: var(--colors-background-primary);
  color: var(--colors-text80);
  font-size: 1rem;
}
html:not(.is-apple) *::-webkit-scrollbar {
  appearance: none;
  width: 13px;
  background: rgba(0, 0, 0, 0);
}
html:not(.is-apple) *::-webkit-scrollbar-thumb {
  background: var(--colors-text50);
  border: 3px solid transparent;
  background-clip: content-box;
  border-radius: 7px;
